import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import "./Home.css";
const Home = ({ page, bg }) => {
  const [loadTitle, setLoadTitle] = useState(false);
  const [loadPara1, setLoadPara1] = useState(false);
  const [loadPara2, setLoadPara2] = useState(false);
  const setLoad = () => {
    setTimeout(() => setLoadTitle(true), 100);
    setTimeout(() => setLoadPara1(true), 300);
    setTimeout(() => setLoadPara2(true), 400);
  };
  const setUnload = () => {
    setLoadTitle(false);
    setLoadPara1(false);
    setLoadPara2(false);
  };
  useEffect(window.location.pathname === "/" ? setLoad : setUnload, [page]);
  return (
    <motion.div
      variants={{
        exit: {
          opacity: 0,
          y: 15,
          transition: {
            duration: 0.3,
            delay: 0.3,
          },
        },
      }}
      exit="exit"
      className={`${bg} w-full h-screen`}
    >
      <div className={`${loadTitle ? "title" : "loading"}`}>
        <div
          className="maintitle text-xl
          pt-20 text-start w-screen sm:text-left sm:px-20
          sm:w-2/3 sm:text-5xl"
        >
          Imagine a place...
        </div>
        <div
          className="w-screen sm:w-4/6 p-10 sm:flex flex-col
          sm:justify-center text-center sm:text-2xl"
        >
          <p
            className={`${
              loadPara1 ? "para1 text-center sm:text-left py-4" : "loadingPara1"
            }`}
          >
            Where you meet people around the world and connect with like-minded
            people, share your thoughts and opinions and discuss your favourite
            topics
          </p>
          <p
            className={`w-screen ${loadPara2 ? "para2 py-4" : "loadingPara2"}`}
          >
            Welcome to React!
          </p>
        </div>
      </div>
    </motion.div>
  );
};
export default Home;
