import { Button } from "@mui/material/";
import { Link, useNavigate } from "react-router-dom";
import logo from "../images/lass.png";
const NavItemsDesk = () => {
  const nav = useNavigate();
  return (
    <nav
      className="hidden stroke sm:flex
    sm:justify-center sm:w-screen"
    >
      <Link to={"/"} className="w-full">
        <img className="flex w-16 pt-1.5 pl-5" src={logo} alt="logo" />
      </Link>
      <ul className="sm:flex sm:row-end-1 sm:justify-end sm:w-full">
        <li>
          <Button
            sx={{
              p: 0,
            }}
            onClick={() => {
              nav("/");
            }}
            disableRipple
          >
            <Link className="px-20 py-4">Home</Link>
          </Button>
        </li>
        <li>
          <Button
            sx={{
              p: 0.01,
            }}
            onClick={() => {
              nav("/about");
            }}
            disableRipple
          >
            <Link className="px-20 py-4">About</Link>
          </Button>
        </li>
        <li>
          <Button
            sx={{
              p: 0,
            }}
            onClick={() => {
              nav("/account");
            }}
            disableRipple
          >
            <Link className="px-20 py-4">Account</Link>
          </Button>
        </li>
      </ul>
    </nav>
  );
};
export default NavItemsDesk;
